@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@200;300;400;600&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
#root,
.page {
  min-height: 100vh;
}

body {
  margin: 0;

  font-family: "Roboto", arial, sans-serif;

  background: url(../resources/img/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  color: white;
}

a {
  color: inherit;
}

.home {
  font-size: 20px;
  // text-align: center;

  .wrapper {
    padding: 2em 1em;
  }

  .banner {
    display: block;
    margin: 0 auto 3em;
    max-width: 1080px;
    width: 100%;
    // padding: 1em;
  }

  // .boxes {
  //   display: flex;
  //   .box {
  //     flex: 1;
  //   }
  // }

  h2 {
    margin-top: 1.5em;
    font-size: 2em;
    text-align: center;
  }

  .info {
    margin: 0 auto;
    max-width: 600px;
  }

  .connection {
    margin: 0 auto;
    text-align: center;
  }

  .btn {
    background: linear-gradient(0deg, #0c6947, #0d9c67);
    margin: 0.5em 0;
    font-size: 2em;
    max-width: 10em;
    width: 100%;
    height: 3em;
    border: none;
    border-radius: 20px;
    color: #fafafa;
    cursor: pointer;

    &:hover {
      background: linear-gradient(0deg, hsl(158, 79%, 17%), hsl(158, 85%, 30%));
    }
  }

  .btn-amount {
    font-size: 0.6em;
  }

  .add-to-metamask {
    text-decoration: underline;
    cursor: pointer;
  }

  .have-untracked {
    text-decoration: underline;
    color: rgb(255, 80, 80);
  }

  .nfts {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;

    .nft {
      text-align: left;
      margin: 1em;
      padding: 1em;
      // border: 2px solid #0c6947;
      width: auto;
      width: 320px;

      background: #0004;
      // border: 2px solid #555;
      border-radius: 20px;

      .nft-address {
      }
      .nft-name-symbol {
      }
    }
  }
}

.contracts {
  color: #ddd;
  font-size: 16px;
  padding-top: 1em;
  text-align: center;
}

.network-options {
  span {
    display: inline-flex;
    align-items: center;
    cursor: pointer;

    margin: 0 8px;
    padding: 8px;
    border-radius: 8px;

    border: 2px solid transparent;

    &:hover,
    &:active {
      // text-decoration: underline;
      // background: #f7f7f7;
      // color: black;
      border: 2px solid #f7f7f7;
    }

    img {
      height: 32px;
      width: 32px;
      margin-right: 8px;
    }
  }
  span.selected {
    background: #f7f7f7;
    color: black;
    &:hover {
      text-decoration: none;
    }
  }
}
